import React,{useEffect, useState} from "react";
import logo from "../assets/bioNest LOGO.svg"
import {Link,graphql,useStaticQuery} from "gatsby"
import Container from "./Container"
import Slider from "../components/UI/slider"


const TopNav = props => {
    const {site} =  useStaticQuery(
        graphql`
        query{            
            site{
                siteMetadata{
                    socialLinks{
                        facebook
                        instagram
                        skype
                    }
                }
            }
        }`
    );
    
    const [flag, setFlag] = useState(false);
    useEffect(()=>{
        if(props.offsetY>100)
        setFlag(true);
        else
        setFlag(false);
    },[props.offsetY]);
    
    const [showSlider, setShowSlider ] = useState(false);

    const NavLink = (props) => {
        return (
            <a href={props.to} className={`${props.className} md:px-4 px-2 md:py-2 py-4  md:transition-text-btw transition-text-wtb `} >{props.children}</a>
        );
    }

    const socialLinks = [
        <NavLink to={site.siteMetadata.socialLinks.facebook} className="px-2">
            <svg className="fill-current w-5" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 408.788 408.788" >
                <path d="M353.701,0H55.087C24.665,0,0.002,24.662,0.002,55.085v298.616c0,30.423,24.662,55.085,55.085,55.085
                h147.275l0.251-146.078h-37.951c-4.932,0-8.935-3.988-8.954-8.92l-0.182-47.087c-0.019-4.959,3.996-8.989,8.955-8.989h37.882
                v-45.498c0-52.8,32.247-81.55,79.348-81.55h38.65c4.945,0,8.955,4.009,8.955,8.955v39.704c0,4.944-4.007,8.952-8.95,8.955
                l-23.719,0.011c-25.615,0-30.575,12.172-30.575,30.035v39.389h56.285c5.363,0,9.524,4.683,8.892,10.009l-5.581,47.087
                c-0.534,4.506-4.355,7.901-8.892,7.901h-50.453l-0.251,146.078h87.631c30.422,0,55.084-24.662,55.084-55.084V55.085
                C408.786,24.662,384.124,0,353.701,0z"/>
            </svg>
        </NavLink>,
        <NavLink to={site.siteMetadata.socialLinks.instagram} className="px-2">
            <svg className="fill-current w-5" viewBox="0 0 511 511.9" xmlns="http://www.w3.org/2000/svg">
                <path d="m510.949219 150.5c-1.199219-27.199219-5.597657-45.898438-11.898438-62.101562-6.5-17.199219-16.5-32.597657-29.601562-45.398438-12.800781-13-28.300781-23.101562-45.300781-29.5-16.296876-6.300781-34.898438-10.699219-62.097657-11.898438-27.402343-1.300781-36.101562-1.601562-105.601562-1.601562s-78.199219.300781-105.5 1.5c-27.199219 1.199219-45.898438 5.601562-62.097657 11.898438-17.203124 6.5-32.601562 16.5-45.402343 29.601562-13 12.800781-23.097657 28.300781-29.5 45.300781-6.300781 16.300781-10.699219 34.898438-11.898438 62.097657-1.300781 27.402343-1.601562 36.101562-1.601562 105.601562s.300781 78.199219 1.5 105.5c1.199219 27.199219 5.601562 45.898438 11.902343 62.101562 6.5 17.199219 16.597657 32.597657 29.597657 45.398438 12.800781 13 28.300781 23.101562 45.300781 29.5 16.300781 6.300781 34.898438 10.699219 62.101562 11.898438 27.296876 1.203124 36 1.5 105.5 1.5s78.199219-.296876 105.5-1.5c27.199219-1.199219 45.898438-5.597657 62.097657-11.898438 34.402343-13.300781 61.601562-40.5 74.902343-74.898438 6.296876-16.300781 10.699219-34.902343 11.898438-62.101562 1.199219-27.300781 1.5-36 1.5-105.5s-.101562-78.199219-1.300781-105.5zm-46.097657 209c-1.101562 25-5.300781 38.5-8.800781 47.5-8.601562 22.300781-26.300781 40-48.601562 48.601562-9 3.5-22.597657 7.699219-47.5 8.796876-27 1.203124-35.097657 1.5-103.398438 1.5s-76.5-.296876-103.402343-1.5c-25-1.097657-38.5-5.296876-47.5-8.796876-11.097657-4.101562-21.199219-10.601562-29.398438-19.101562-8.5-8.300781-15-18.300781-19.101562-29.398438-3.5-9-7.699219-22.601562-8.796876-47.5-1.203124-27-1.5-35.101562-1.5-103.402343s.296876-76.5 1.5-103.398438c1.097657-25 5.296876-38.5 8.796876-47.5 4.101562-11.101562 10.601562-21.199219 19.203124-29.402343 8.296876-8.5 18.296876-15 29.398438-19.097657 9-3.5 22.601562-7.699219 47.5-8.800781 27-1.199219 35.101562-1.5 103.398438-1.5 68.402343 0 76.5.300781 103.402343 1.5 25 1.101562 38.5 5.300781 47.5 8.800781 11.097657 4.097657 21.199219 10.597657 29.398438 19.097657 8.5 8.300781 15 18.300781 19.101562 29.402343 3.5 9 7.699219 22.597657 8.800781 47.5 1.199219 27 1.5 35.097657 1.5 103.398438s-.300781 76.300781-1.5 103.300781zm0 0"/><path d="m256.449219 124.5c-72.597657 0-131.5 58.898438-131.5 131.5s58.902343 131.5 131.5 131.5c72.601562 0 131.5-58.898438 131.5-131.5s-58.898438-131.5-131.5-131.5zm0 216.800781c-47.097657 0-85.300781-38.199219-85.300781-85.300781s38.203124-85.300781 85.300781-85.300781c47.101562 0 85.300781 38.199219 85.300781 85.300781s-38.199219 85.300781-85.300781 85.300781zm0 0"/><path d="m423.851562 119.300781c0 16.953125-13.746093 30.699219-30.703124 30.699219-16.953126 0-30.699219-13.746094-30.699219-30.699219 0-16.957031 13.746093-30.699219 30.699219-30.699219 16.957031 0 30.703124 13.742188 30.703124 30.699219zm0 0"/>
            </svg>
        </NavLink>,
        <NavLink to={site.siteMetadata.socialLinks.skype} className="px-2">
            <svg className="fill-current w-5" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                <path d="m23.309 14.547c1.738-7.81-5.104-14.905-13.139-13.543-4.362-2.707-10.17.352-10.17 5.542 0 1.207.333 2.337.912 3.311-1.615 7.828 5.283 14.821 13.311 13.366.936.495 2.004.777 3.139.777 4.949 0 8.118-5.133 5.947-9.453zm-11.213 5.332c-2.342 0-4.63-.565-5.984-2.499-2.018-2.854.643-4.282 1.949-3.194 1.09.919.748 3.142 3.949 3.142 1.411 0 3.153-.602 3.153-2.001 0-2.815-6.245-1.483-8.728-4.552-.912-1.124-1.084-3.107.036-4.545 1.952-2.512 7.68-2.665 10.143-.768 2.274 1.76 1.66 4.096-.175 4.096-2.207 0-1.047-2.888-4.61-2.888-2.583 0-3.599 1.837-1.78 2.731 2.421 1.202 8.75.827 8.75 5.603-.008 3.357-3.247 4.875-6.703 4.875z" />
            </svg>
        </NavLink>
    ];

    const NavigationLinks =[ 
        <NavLink to="#about" >About</NavLink>,
        <NavLink to="#products" >Focus Area</NavLink>,
        <NavLink to="#contact" >Contact</NavLink>
    ];

    return (
        <>
        <div className={`z-30 fixed w-full flex justify-center bg-gray-100 ${flag?"bg-opacity-100 md:shadow shadow-sm ":"bg-opacity-50"} transition-all duration-500 `} id={props.id}>
        <Container className={`flex items-center justify-between md:py-4 py-2 uppercase font-semi-bold z-30 md:text-base text-xs`}>
            <div className="">
                <a href="#home"><img className="md:h-16 h-12 md:px-2 px-1" src={logo} alt="BioNestLogo"/></a>
            </div>
            <div className="md:flex items-center hidden ">
                {NavigationLinks}
                <div className="flex items-center divide-x divide-white">
                {socialLinks}
                </div>
            </div>
            <button className={`md:hidden flex items-center transition-text-btw focus:outline-none`} onClick={()=>setShowSlider(true)}>
            <svg className="fill-current w-5" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384.97 384.97" >
                    <path d="M12.03,120.303h360.909c6.641,0,12.03-5.39,12.03-12.03c0-6.641-5.39-12.03-12.03-12.03H12.03
                        c-6.641,0-12.03,5.39-12.03,12.03C0,114.913,5.39,120.303,12.03,120.303z"/>
                    <path d="M372.939,180.455H12.03c-6.641,0-12.03,5.39-12.03,12.03s5.39,12.03,12.03,12.03h360.909c6.641,0,12.03-5.39,12.03-12.03
                        S379.58,180.455,372.939,180.455z"/>
                    <path d="M372.939,264.667H132.333c-6.641,0-12.03,5.39-12.03,12.03c0,6.641,5.39,12.03,12.03,12.03h240.606
                        c6.641,0,12.03-5.39,12.03-12.03C384.97,270.056,379.58,264.667,372.939,264.667z"/>
            </svg>
            </button>
        </Container>
        </div>
        <Slider close={setShowSlider} show={showSlider}>
            <div className="flex flex-col uppercase text-xs h-full" onClick={()=>setShowSlider(false)}>
                <div className="flex flex-col divide-y">
                    {NavigationLinks}
                </div>
                <div className="flex divide-x py-4 self-center">
                    {socialLinks}
                </div>

            </div>
        </Slider>
        </>
    );
} 

export default TopNav; 